<template>
  <div class="main-div">
    <div class="main-bg">
      <div class="contain">
        <div class="description-content">
          <p class="description">本地區的怪力密度下降</p>
          <p class="description">人們也恢復正常狀態</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CommuteKimEnd',
  mounted () {
    this.$store.commit('changeHeader', true)
    this.$store.commit('changeFrameDot', true)
    this.$store.commit('kimWin', true)
    this.$store.commit('getKimCard', 'color')
    // 塞入 local storage
    let vuesList = JSON.parse(localStorage.getItem('vuexList'))
    vuesList.isKimWin = true
    vuesList.kimCardType = 'color'
    localStorage.setItem('vuexList', JSON.stringify(vuesList))
    //
  }
}
</script>

<style lang="scss" scoped>
  @import "~@/assets/scss/common.scss";
  .main-div {
    @extend fullScreenImage;
    background-image: url('~@/assets/images/commute/main-bg.png');
    .main-bg {
      @extend fullScreenImage;
      background-image: url('~@/assets/images/commute/white-map.png');
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      .contain {
        .description-content {
          .description {
            @extend descriptionWhiteText;
            margin: 5px 0px;
            text-align: center;
          }
        }
      }
    }
  }
</style>



